import React, { FunctionComponent } from 'react';
import getComponent from '../../../utils/cms/component-resolver';

import ClassNames from './SplitContainer.module.scss';
import AppLink from '../../../util-components/Link/Link';

interface Props {
    area1: any[];
    area2: any[];
}

const SplitContainer: FunctionComponent<Props> = ({ area1, area2 }) => {
    function toggleRtl(): void {
        document.documentElement && document.documentElement.classList.toggle('rtl');
    }

    function renderArea(index: number, items: any[]): React.ReactNode {
        return (
            <div className={ClassNames['area' + index]}>
                <h3>Area{index} from CMS SplitContainer</h3>
                <div>{items.map(item => getComponent(item))}</div>
                {index === 2 && (
                    <div>
                        <div>
                            <AppLink to="/blank-page">Second page (hard link now)</AppLink>
                        </div>
                        <div>
                            <AppLink to="/splash">Splash (hard link now)</AppLink>
                        </div>
                        <div>
                            <button onClick={toggleRtl}>Toggle RTL</button>
                        </div>
                    </div>
                )}
            </div>
        );
    }

    return (
        <div className={ClassNames.SplitContainer}>
            {renderArea(1, area1)}
            {renderArea(2, area2)}
        </div>
    );
};

export default SplitContainer;
