import React, { FunctionComponent } from 'react';
import ClassNames from './SplitPage.module.scss';
import SplitContainer from '../../SplitContainer/SplitContainer';
import PageRoot from '../PageRoot/PageRoot';
import { CmsPageContext } from '../../../../types/CmsPageContext';

interface Props {
    pageContext: {
        fields: {
            area1: any[];
            area2: any[];
        };
    };
}

const SplitPage: FunctionComponent<Props & CmsPageContext> = props => {
    const { area1, area2 } = props.pageContext.fields;
    return (
        <PageRoot {...props.pageContext.meta}>
            <div className={ClassNames.SplitPage}>
                <SplitContainer area1={area1} area2={area2} />
            </div>
        </PageRoot>
    );
};

export default SplitPage;
